.logo{
    height: 100px;
    width: 120px;
    

}
.nav-container{

    display: flex;
    justify-content: space-between;
    height: 80px;
    background-color:#7f4a88;
    border-radius: 50px;
    color: white;
}

.nav-ul{
    width: 600px;
    display: flex;
    list-style: none;
margin-top: auto;
height: 80px;
justify-content: space-around;
align-items: center;
font-size: large;
}
.nav-ul li{
    color: white;
    text-decoration: none;
}

.image{
    margin-top: 20px;
    height: 450px;
    width: 650px;
}
.maindiv{
    display: flex;
}
.headingdiv{
    width: 100%;
    font-size: 40px;
    text-align: center;
    color: #7f4a88 ;
    margin-top: 40px;
}

.cardimage {
    height: 220px; 
    width: 250px; 
    border-radius: 10px; 
    position: relative; 
    bottom: 12px;
   
    overflow: hidden; 
    transition: transform 0.3s, box-shadow 0.3s; 
}

.cardimage img {
    height: 100%;
    width: 100%; 
    object-fit: cover; 
    transition: transform 0.3s; 
}

.cardimage:hover img {
    transform: scale(1.1);
}

.cardimage:hover {
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3); 
}



.cardmaindiv {

    height: 380px;
    width: 350px;
    border-radius: 10px;
    text-align: center;
    padding: 10px;
    background-color: white; 
    transition: transform 0.3s, box-shadow 0.3s; 
}

.cardmaindiv:hover {
    transform: translateY(-10px); 
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2); 
    border-color: #9b5b9b; 
    border: 2px solid  #9b5b9b;
}



.mapdiv{
    justify-content:space-evenly;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    gap: 20px;
    margin-top: 50px;

}

.cart {
    height: 40px; 
    border-radius: 10px; 
    background-color: #7f4a88; 
    color: white; 
    display: flex; 
    justify-content: center; 
    align-items: center;
    padding: 0 20px; 
    font-size: 16px; 
    font-weight: bold; 
    border: none; 
    cursor: pointer; 
    transition: background-color 0.3s, transform 0.3s; 
}

.cart:hover {
    background-color: #9b5b9b; 
    transform: scale(1.05); 
}

.cart:active {
    transform: scale(0.95); 
}


.cartmaindiv{
    background-color: #f0b8f0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 92px;
    border-radius: 10px;
    gap: 20px;
}
.footer {
    background-color: #7f4a88; 
    color: white; 
    padding: 20px 0;
    border-radius: 30px;
    


}

.footer-container {
    display: flex; 
    justify-content: space-around;
    max-width: 1200px;
    margin: 0 auto; 
}

.footer-section {
    flex: 1; 
    padding: 0 20px; 
}

.footer-section h3 {
    margin-bottom: 10px;
}

.footer-section p {
    margin-bottom: 15px;
}

.footer-section ul {
    list-style-type: none; 
    padding: 0; 
}

.footer-section ul li {
    margin-bottom: 10px; 
}

.footer-section ul li a {
    color: white; 
    text-decoration: none;
}

.footer-section ul li a:hover {
    text-decoration: underline; 
}

.footer-bottom {
    text-align: center; 
    margin-top: 20px; 
}

.about-us {
    padding: 20px;
    max-width: 800px;
    margin: auto;
    background-color: #f0b8f0;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    opacity: 0;
    animation: fadeIn 1s forwards; 
    margin-top: 15px;
    
    
}

.about-us h1 {
    font-size: 2.5em;
    color: white;
    margin-bottom: 10px;
}

.about-us h2 {
    font-size: 2em;
    color: white;
    margin-top: 20px;
}

.about-us p {
    font-size: 1.1em;
    line-height: 1.6;
    color: black;
}


@keyframes fadeIn {
    from {
        opacity: 0; 
    }
    to {
        opacity: 1; 
    }
}
.contact {
    padding: 20px;
    max-width: 800px;
    margin: auto;
    background-color: #f0b8f0 ;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    opacity: 0; 
    animation: fadeIn 1s forwards; 
    margin-top: 15px;
}

.contact h1 {
    text-align: center;
    color: #4b0082; 
}

.contact-details {
    display: flex; 
    justify-content: space-between; 
    margin-top: 20px;
}

.team-info, .address-info {
    flex-basis: 48%;
    background-color: white; 
    border-radius: 8px;
    padding: 15px;
}

.team-image {
    width: 100%;
    border-radius: 8px;
}

.team-info h2,
.address-info h2 {
    color:black;
}

.team-info h3,
.team-info h4,
.address-info h4 {
    color:black; 
}


@keyframes fadeIn {
    from {
        opacity: 0; 
    }
    to {
        opacity: 1; 
    }
}

    .tag{
        height: 50px; 
        border-radius: 10px; 
        background-color: #7f4a88; 
        color: white; 
        display: flex; 
        justify-content: center; 
        align-items: center;
        padding: 0 20px; 
        font-size: 16px; 
        font-weight: bold; 
        border: none; 
        cursor: pointer; 
        transition: background-color 0.3s, transform 0.3s; 
    }
    
    .tag:hover {
        background-color: #9b5b9b; 
        transform: scale(1.05); 
    }
    
    .tag:active {
        transform: scale(0.95); 
    }
    .tag:hover {
        background-color: #9b5b9b;
        transform: scale(1.05);
    }


.btndiv{
    display: flex;
    height: 50px;
    width: 60%;
    justify-content: space-around;

   

}
.mainbtndiv{
    display: flex;
    justify-content: space-between;
  
    
}
.searchbar {
    display: flex; 
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: 80%; 
    max-width: 500px; 
    margin-top: 5px;
    position: relative;
    left: 70px;
   
}


.search-input {
    flex-grow: 1; 
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}


.search-input:focus {
    border-color: #9b5b9b; 
    box-shadow: 0 0 5px rgba(0, 150, 136, 0.5);
}


.searchbtn {
    padding: 10px;
    font-size: 16px;
    background-color: #9b5b9b; 
    color: white; 
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}


.searchbtn:hover {
    background-color:#9b5b9b;
    transform: scale(1.05);
}


.searchbtn:active {
    transform: scale(0.95); 
}


@media (max-width: 576px) {
    .searchbar {
        flex-direction: column; 
        gap: 15px; 
        width: 90%; 
    }

    .searchbtn {
        width: auto; 
        align-self: stretch; 
    }
}